import React from "react";
import "./Greeting.css";
import SocialMedia from "../../components/socialMedia/SocialMedia";
import Button from "../../components/button/Button";
import { Fade } from "react-reveal";
import "../../RubberBand.css";
import $ from "jquery";
import pdf from "../../assests/docs/resume.pdf";

class Greeting extends React.Component {
  componentDidMount() {
    $(document).ready(function () {
      $(".rubberBand").bind(
        "webkitAnimationEnd mozAnimationEnd animationend",
        function () {
          $(this).removeClass("animated");
        }
      );

      $(".rubberBand").hover(function () {
        $(this).addClass("animated");
      });
    });
  }

  render() {
    return (
      <Fade bottom duration={2000} distance="40px">
        <div className="greet-main" id="greeting">
          <div className="greeting-main">
            <div className="greeting-text-div">
              <div>
                <h1 className="greeting-text" style={{ color: "#ea5455" }}>
                  <span class="rubberBand">H</span>
                  <span class="rubberBand">i</span>
                  <span class="rubberBand">,</span>{" "}
                  <span class="rubberBand">I</span>{" "}
                  <span class="rubberBand">a</span>
                  <span class="rubberBand">m</span>{" "}
                  <span class="rubberBand">S</span>
                  <span class="rubberBand">a</span>
                  <span class="rubberBand">b</span>
                  <span class="rubberBand">i</span>
                  <span class="rubberBand">h</span>
                </h1>
                <p
                  className="greeting-text-p subTitle"
                  style={{ color: "#decdc3" }}
                >
                  <span class="rubberBand">I</span>{" "}
                  <span class="rubberBand">am</span>{" "}
                  <span class="rubberBand">a</span>{" "}
                  <span class="rubberBand">recent</span>{" "}
                  <span class="rubberBand">RMIT</span>{" "}
                  <span class="rubberBand">University</span>{" "}
                  <span class="rubberBand">graduate.</span>{" "}
                  <span class="rubberBand">I</span>{" "}
                  <span class="rubberBand">currently</span>{" "}
                  <span class="rubberBand">work</span>{" "}
                  <span class="rubberBand">as</span>{" "}
                  <span class="rubberBand">a</span>{" "}
                  <span class="rubberBand">freelance</span>{" "}
                  <span class="rubberBand">iOS</span>{" "}
                  <span class="rubberBand">developer</span>{" "}
                  <span class="rubberBand">and</span>{" "}
                  <span class="rubberBand">a</span>{" "}
                  <span class="rubberBand">web</span>{" "}
                  <span class="rubberBand">developer</span>{" "}
                  <span class="rubberBand">and</span>{" "}
                  <span class="rubberBand">I</span>{" "}
                  <span class="rubberBand">am</span>{" "}
                  <span class="rubberBand">open</span>{" "}
                  <span class="rubberBand">for</span>{" "}
                  <span class="rubberBand">new</span>{" "}
                  <span class="rubberBand">opportunities.</span>{" "}
                  <span class="rubberBand">I</span>{" "}
                  <span class="rubberBand">am</span>{" "}
                  <span class="rubberBand">also</span>{" "}
                  <span class="rubberBand">a</span>{" "}
                  <span class="rubberBand">hobbyist</span>{" "}
                  <span class="rubberBand">photographer.</span>{" "}
                </p>
                <SocialMedia />
                <div className="portfolio-repo-btn-div">
                  <Button
                    text="SEE MY RESUME"
                    newTab={true}
                    href={pdf}
                    className="portfolio-repo-btn"
                  />
                </div>
              </div>
            </div>
            <div className="landingpic-wrapper">
              <div className="greeting-image-div">
                <img
                  className="my-pic"
                  src={require(`../../assests/images/mypic.jpg`)}
                  alt="portrait of sabih"
                />
                <div className="square-shape"></div>
              </div>
            </div>
          </div>
        </div>
      </Fade>
    );
  }
}
export default Greeting;
